// src/app/empresas/[seo_url]/page.tsx
import React from 'react'
import TabNavigation from '@/components/empresa/TabNavigation'
import AboutTab      from '@/components/empresa/tabs/AboutTab'
import ServicesTab   from '@/components/empresa/tabs/ServicesTab'
import PortfolioTab  from '@/components/empresa/tabs/PortfolioTab'
import ReviewsTab    from '@/components/empresa/tabs/ReviewsTab'
import FaqTab        from '@/components/empresa/tabs/FaqTab'
import ContactTab    from '@/components/empresa/tabs/ContactTab'

interface Company {
  name: string
  seo_url: string
  // … outros campos que você retorne da API Rails
  reviews: any[]
  faqs: any[]
  contact: any
}

export default async function EmpresaPage({ params }: { params: { seo_url: string } }) {
  const res = await fetch(`http://localhost:3001/companies/${params.seo_url}.json`, { cache: 'no-store' })
  if (!res.ok) throw new Error('Empresa não encontrada')
  const company: Company = await res.json()

  return (
    <div className="font-sans bg-gray-50 text-gray-800">
      <header className="relative">
        {/* copie aqui todo o HTML/Tailwind do seu banner */}
      </header>

      <main className="max-w-6xl mx-auto px-4 py-8">
        <TabNavigation />
        <div className="mt-6">
          <AboutTab    />
          <ServicesTab />
          <PortfolioTab />
          <ReviewsTab  reviews={company.reviews} />
          <FaqTab      items={company.faqs} />
          <ContactTab  info={company.contact} />
        </div>
      </main>

      <footer className="bg-blue-900 text-white py-12">
        {/* seu footer estático */}
      </footer>
    </div>
  )
}
